<template>
	<view class="">
		<!-- 导航栏组件 -->
		<navigationBar :address="true" title='小兔鲜' titleColor="#27bb9a"></navigationBar>

		<!-- 头部 -->
		<view class="head-info flex justify-around">
			<view class="" v-for="(item,i) in headList" :key="item.id">
				<view @click="changeHead(i)" class="head-text text-center">
					<text class="padding-sm">{{item.name}}</text>
				</view>
			</view>
		</view>

		<!-- 点击头部显示的数据 -->
		<u-overlay :show="headShow" @click="headShow = false" opacity="0.1">
			<view class="head-data flex justify-between flex-wrap" @tap.stop>
				<view @click="headCategory(item)" class="head-data-con padding" v-for="(item,i) in headSonList"
					:key="item.id">
					<image class="" :src="item.picture" mode=""></image>
					<view class="">
						<text class="fs-16">{{item.name}}</text>
					</view>

				</view>

			</view>
		</u-overlay>

		<!-- 轮播图 -->
		<view class="banner">
			<swiper :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000"
				indicator-active-color="#69bdb0">
				<swiper-item v-for="(item,i) in BannerList" :key="item.id">
					<view class="banner-img">
						<image :src="item.imgUrl" mode="aspectFill"></image>
					</view>
				</swiper-item>
			</swiper>
		</view>

		<!-- 品牌 -->
		<view class="brands flex justify-between">
			<view @click="brandClick(item)" class="brands-img" v-for="(item,i) in brandFliter" :key="i">
				<image :src="item.picture" mode="aspectFill"></image>
				<view class="fs-12 brands-text">
					<text>{{item.name}}</text>
				</view>
			</view>
		</view>

		<u-popup :show="brandShow" mode="center" round="50" @close="brandShow=false">
			<view class="flex align-center">
				<image class="brandImage" :src="brandDetailsList.brandImage" mode=""></image>
				<view class="brandDetails color-huise padding">
					<view class="padding-tb-sm">
						品牌:<text class="margin-right-sm">{{brandDetailsList.name}}</text>
						宣传语:<text>{{brandDetailsList.slogan}}</text>
					</view>
					<view class="">
						产地:<text class="margin-right-sm">{{brandDetailsList.productionPlace}}</text>
						英文名:<text>{{brandDetailsList.nameEn}}</text>
					</view>
				</view>
			</view>
			<view class="brand-logo">
				<image v-if="brandDetailsList.logo !== null" class="" :src="brandDetailsList.logo" mode="aspectFit"></image>
				<text v-else class="">LOGO</text>
			</view>
			
		</u-popup>

		<!-- 新鲜好物 -->
		<homeModulesData :list="newList" title="新鲜好物" str="新鲜出炉 品质靠谱" :flag="true"></homeModulesData>
		<!-- 人气推荐 -->
		<homeModulesData :list="hotList" title="人气推荐" str="人气爆款 不容错过"></homeModulesData>
	</view>
</template>

<script>
	import {
		getHome,
		getBanner,
		getHead,
		getNew,
		getHot,
		getBeand,
		getBrandId
	} from '../../api/home.js'
	import {
		gCategory
	} from '../../api/goods.js'
import { data } from 'uview-ui/libs/mixin/mixin';
	export default {
		data() {
			return {
				// 头部数据
				headList: [],
				headSonList: [],
				headShow: false,
				// 头部导航
				headCategoryObj: {
					categoryId: '',
					sortField: '',
					sortMethod: 'desc',
					page: 1,
					pagsize: 10
				},
				// 头部导航分类数据
				headCateList:{},
				// 广告轮播图数据
				BannerList: [],
				// 新鲜好物数据
				newList: [],
				// 人气推荐数据
				hotList: [],
				// 热门品牌数据
				brandList: [],
				brandShow: false,
				brandDetailsList:[]

			};
		},

		// 页面加载时就调用
		async onLoad() {
			this.head()
			this.banner()
			this.getModulesData()
		},
		methods: {
			// 获取head数据
			async head() {
				const res = await getHead()
				this.headList = res.data.result
			},
			// 点击头部显示对应数据
			changeHead(index) {
				this.headShow = true
				this.headList.forEach((item, i) => {
					if (index === i) {
						this.headSonList = item.children
						return this.headSonList
					}
				})
			},
			async headCategory(item) {
				this.headCategoryObj.categoryId = item.id
				const res = await gCategory(this.headCategoryObj)
				this.headCateList = res.data.result
				uni.navigateTo({
					url: '/pages/category/category',
					events:{
						// 默认排序
						default:async (dra) => {
							const res = await gCategory(this.headCategoryObj)
						},
						// 最新商品
						newCom:async (data) => {
							// 监听下一个页面传递的数据及回传的数据
							let {sortField,sortMethod} = this.headCategoryObj
							sortField = data.data
							sortMethod = ''
							const res = await gCategory(this.headCategoryObj)
						},
						// 最高人气
						numMax:async (data) => {
							this.headCategoryObj.sortField = data.data
							const res = await gCategory(this.headCategoryObj)
						},
						// 价格排序
						priceDesc:async (data) => {
							this.headCategoryObj.sortField = data.data
							const res = await gCategory(this.headCategoryObj)
						},
						reBottom:async (data) => {
							this.headCategoryObj.page++
							console.log(this.headCategoryObj.page);
							const res = await gCategory(this.headCategoryObj)
						}
					},
					success: (res2) => {
						// 通过eventChannel向被打开页面传送数据
						res2.eventChannel.emit('headCate', {
							data:res.data.result
						})
					}
				})
			},
			// 获取广告轮播图数据
			async banner() {
				const res = await getBanner()
				this.BannerList = res.data.result
			},

			// 获取首页类别模块数据
			async getModulesData() {
				// 新鲜好物
				const res = await getNew()
				// console.log(res);
				this.newList = res.data.result

				// 人气推荐
				const res2 = await getHot()
				this.hotList = res2.data.result

				// 热门品牌
				const res3 = await getBeand()
				this.brandList = res3.data.result
				// console.log(res3.data.result);
			},
			// 品牌详情
			async brandClick(item) {
				const res = await getBrandId(item.id)
				this.brandDetailsList = res.data.result
				this.brandShow = true
			},
		},

		computed: {
			// 筛选品牌显示数量
			brandFliter() {
				return this.brandList.filter((item, index) => {
					return index < 5
				})
			}
		}
	}
</script>

<style lang="scss">
	page {
		margin-top: 100rpx;
	}

	.banner {
		.banner-img {
			image {
				width: 100%;
				height: 300rpx;
			}

		}
	}

	.brands {
		background-color: #f5f7f9;
		padding: 20rpx;
		margin: 20rpx -10rpx 20rpx 0;
		height: 150rpx;

		.brands-img {
			image {
				border-radius: 50%;
				height: 100rpx;
				width: 100rpx;
				background-color: powderblue;
				margin-right: 10rpx;

			}

			.brands-text {
				width: 100rpx;
				text-align: center;
			}
		}
	}
	
	.brand-logo{
		display: flex;
		image{
			margin: auto;
			width: 200rpx;
			height: 200rpx;
		}
		text{
			margin: auto;
			font-weight: 800;
			padding: 40rpx;
			font-size: 50rpx;
			text-align: center;
		}
	}
	
	.brandDetails {
		height: 300rp;
		
	}
	.brandImage {
		// margin-bottom: -30rpx;
		border-radius: 50% 30% 20% 5%;
		width: 150rpx;
		height: 150rpx;
	}

	.head-info {
		width: 100%;
		height: 120rpx;
		padding-top: 20rpx;
		overflow: auto;
		padding-left: 450rpx;
		position: relative;
		left: 0;
		background-color: #f5f7f9;

		.head-text {
			width: 130rpx;
			margin-left: 5rpx;
			background-color: #ffffff;
			height: 80rpx;
			line-height: 80rpx;
			// border-left: 1rpx solid gray;
		}
	}

	.head-data {
		width: 100%;
		height: 300rpx;
		background-color: #ffffff;
		position: absolute;
		left: 0;
		opacity: 0.9;

		.head-data-con {
			image {
				width: 80rpx;
				height: 80rpx;
			}
		}
	}

	::v-deep .u-transition {
		margin-top: 194rpx;
	}
</style>
